<template>
  <div>
    <h1>Hello world!</h1>

    <div v-for="level in data">
      <h2>{{level.name}}</h2>
      <div v-for="poem in level.poems">
        <input type="checkbox" v-model="poem.name">
      </div>
    </div>
  </div>
</template>

<script>
import constData from './assets/data'

let data = constData;

data = data.map((level) => {

  level.poems = level.poems.map((poem) => {
    poem.isSelect = false;
    return poem;
  });

  return level;
});

export default {
  data() {
    return {
      data
    }
  }
}
</script>

<style>
</style>



